import {
  DownloadOutlined,
  PlusOutlined,
  PrinterOutlined,
  UploadOutlined,
} from "@ant-design/icons";
import {
  CDropTable,
  CError,
  CLoadingSpinner,
  CTable,
  CTitle,
} from "@components";
import {
  FDownloadTemplate,
  FGetAccess,
  FGetTableData,
  FImportData,
  FReadFile,
  FRemove,
} from "@function";
import Link from "next/link";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

function Asset() {
  const router = useRouter();
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
  });
  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
  }, []);
  useEffect(() => {
    getData();
  }, [paginate]);
  const getData = () => {
    FGetTableData(setLoading, "api/asset", paginate, setData, setTotal);
  };

  const columns = [
    {
      title: "Barcode",
      dataIndex: "tag_number",
      key: "tag_number",
      width: 100,
      align: "center",
      ellipsis: true,
    },
    {
      title: "No Induk Barang",
      dataIndex: "no_induk_barang",
      width: 150,
      key: "no_induk_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "AMS NO",
      dataIndex: "no_barang",
      width: 100,
      key: "no_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "REINS NO",
      dataIndex: "old_number",
      width: 100,
      key: "old_number",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Name",
      key: "name",
      width: 400,
      align: "center",
      ellipsis: true,
      render: (item) => (
        <Link
          style={{ color: "#1890ff" }}
          href={"asset/" + JSON.stringify(item.id)}
        >
          {item.name}
        </Link>
      ),
    },
    {
      title: "Branch",
      key: "branch",
      align: "center",
      dataIndex: "branch",
      ellipsis: true,
    },
    {
      title: "Departement",
      align: "center",
      width: 200,
      key: "vendor",
      dataIndex: "vendor",
      ellipsis: true,
    },
    {
      title: "Ownership",
      key: "ownership",
      align: "center",
      dataIndex: "ownership",
      ellipsis: true,
    },
    {
      title: "Code Activa",
      key: "code_activa",
      align: "center",
      width: 200,
      dataIndex: "location",
      ellipsis: true,
    },
    {
      title: "Location",
      align: "center",
      width: 200,
      key: "location",
      dataIndex: "location",
      ellipsis: true,
    },
    {
      title: "Tanggal Perolehan",
      dataIndex: "tgl_perolehan",
      key: "tgl_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Jenis Barang",
      key: "asset_type",
      align: "center",
      dataIndex: "asset_type",
      ellipsis: true,
    },
    {
      title: "Payment Voucher",
      key: "payment_voucher",
      align: "center",
      dataIndex: "payment_voucher",
      ellipsis: true,
    },
    {
      title: "Merek Barang",
      key: "merek_barang",
      align: "center",
      dataIndex: "merek_barang",
      ellipsis: true,
    },
    {
      title: "Model",
      key: "model",
      align: "center",
      dataIndex: "model",
      ellipsis: true,
    },
    {
      title: "Harga Perolehan",
      key: "harga_perolehan",
      align: "center",
      ellipsis: true,
      width: 125,
      render: (item) => {
        return item.currency_perolehan == "IDR"
          ? new Intl.NumberFormat("id-ID", {
              style: "currency",
              currency: "IDR",
            }).format(Number(item.harga_perolehan))
          : new Intl.NumberFormat("en-US", {
              style: "currency",
              currency: "USD",
            }).format(Number(item.harga_perolehan));
      },
    },
    {
      title: "Currency Perolehan",
      dataIndex: "currency_perolehan",
      key: "currency_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Serial Number",
      key: "serial_number",
      align: "center",
      dataIndex: "serial_number",
      ellipsis: true,
    },
    {
      title: "Anggaran Asset",
      key: "anggaran",
      align: "center",
      dataIndex: "anggaran",
      ellipsis: true,
    },
    {
      title: "Masa Manfaat (Year)",
      dataIndex: "masa_manfaat",
      key: "masa_manfaat",
      ellipsis: true,
      align: "center",
      render: (item) => {
        return item === null ? "0" : item;
      },
    },
    {
      title: "Flag",
      dataIndex: "flag",
      key: "flag",
      align: "center",
    },
    {
      title: "Status Activa",
      key: "status_activa",
      align: "center",
      dataIndex: "status_activa",
      ellipsis: true,
    },
    {
      title: "Remark",
      key: "remark",
      dataIndex: "remark",
      align: "center",
      width: 200,
      ellipsis: true,
    },
    {
      title: "Kondisi Barang",
      key: "status",
      dataIndex: "status",
      fixed: "right",
      align: "center",
      width: 150,
      ellipsis: true,
    },
    {
      title: "Action",
      key: "action",
      align: "center",
      fixed: "right",
      dataIndex: "id",
      width: 100,
      render: (item) =>
        accessList.update || accessList.delete || accessList.view ? (
          <CDropTable
            items={[
              {
                action: () => printLabel(item),
                icon: "printer",
                name: "Print Label",
                access: accessList.view,
              },
              {
                action: () => handleEdit(item),
                icon: "edit",
                name: "Edit",
                access: accessList.update,
              },
              {
                action: () => handleRemove(item),
                icon: "delete",
                name: "Hapus",
                access: accessList.delete,
              },
            ]}
          />
        ) : null,
    },
  ];

  const onShowSizeChange = (current, pageSize) => {
    setPaginate({
      ...paginate,
      pageSize: pageSize,
      current_page: current,
    });
  };
  //* Crud
  const handleInsert = (e) => {
    router.push("/asset/add");
  };
  const handleRemove = (id) => {
    FRemove(id, "api/asset", getData);
  };
  const handleEdit = (id) => {
    router.push("/asset/edit/" + id);
  };
  const handleImport = () => {
    const f = document.getElementById("input-file");
    f.click();
  };

  const handleDownload = async () => {
    FDownloadTemplate("api/template/asset", "Template Asset.xlsx");
  };

  const onReadFile = (file) => {
    FReadFile(file.target.files[0], dataToImport);
  };

  const dataToImport = (res) => {
    var data = res.map((r) => {
      return {
        tag_number: r[1] || "",
        no_induk_barang: r[2] || "",
        old_number: r[3] || "",
        name: r[4] || "",
        branch: r[5] || "",
        departement: r[6] || "",
        ownership: r[7] || "",
        codeactiva: r[8] || "",
        location: r[9] || "",
        tgl_perolehan: r[10] || "",
        jenis_barang: r[11] || "",
        payment_voucher: r[12] || "",
        merek: r[13] || "",
        model: r[14] || "",
        harga_perolehan: r[15] || "",
        currensy: r[16] || "",
        serial_number: r[17] || "",
        anggaran_asset: r[18] || "",
        masa_manfaat: r[19] || "",
        flag: r[20] || "",
        status_activa: r[21] || "",
        status: r[22] || "",
        remark: r[23] || "",
      };
    });
    onImportData(data);
  };

  const onImportData = (data) => {
    FImportData("api/import/asset", data, getData, setLoading, "input-file");
  };
  const handleCreationForm = () => {
    router.push("/asset/parent-creation-assets");
  };
  const printLabel = (param) => {
    window.open(process.env.NEXT_PUBLIC_BASE_URL + "asset/label?id=" + param);
  };

  const printLabels = () => {
    let params = {
      page: paginate.current_page,
      keyword: paginate.keyword,
      ...paginate,
    };
    params = new URLSearchParams(params).toString();
    window.open(process.env.NEXT_PUBLIC_BASE_URL + "asset/label?" + params);
  };
  //* End Crud

  function handleSearch(e) {
    setPaginate({
      ...paginate,
      [e.target.id]: e.target.value,
    });
  }
  const onRefresh = () => {
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    getData();
  };

  const items = [
    {
      key: "1",
      label: (
        <div onClick={handleInsert}>
          <PlusOutlined /> Add
        </div>
      ),
    },
    {
      key: "2",
      label: (
        <div onClick={handleCreationForm}>
          <PlusOutlined /> Parent Creation Assets
        </div>
      ),
    },
    {
      key: "3",
      label: (
        <div onClick={handleImport}>
          <UploadOutlined /> Import Asset
        </div>
      ),
    },
    {
      key: "4",
      label: (
        <div onClick={handleDownload}>
          <DownloadOutlined /> Download Template
        </div>
      ),
    },
    {
      key: "5",
      label: (
        <div onClick={printLabels}>
          <PrinterOutlined /> Print Labels
        </div>
      ),
    },
  ];

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  return (
    <>
      <CTitle />
      <CTable
        total={total}
        onShowSizeChange={onShowSizeChange}
        data={data}
        columns={columns}
        items={items}
        scroll={{ x: 4000, y: 350 }}
        paginate={paginate}
        handleSearch={handleSearch}
        refresh={onRefresh}
        loading={loading}
        accessPermission={accessList}
      />
      <input
        type="file"
        style={{ display: "none" }}
        id="input-file"
        onChange={(file) => onReadFile(file)}
        name="file"
        accept=".xlsx, .xls"
      />
    </>
  );
}

export default Asset;
